<template>
    <form @submit.prevent="addTodo">
        <div>
            <input type="text"
            placeholder="enter tasks"
            v-model="enteredTask.name">
            <button>Add task</button>
        </div>
    </form>
</template>

<script>
export default {
  data () {
    return {
      enteredTask: {
        name: '',
        active: false
      }
    }
  },
  emits: ['add-todo'],
  methods: {
    addTodo () {
      this.$emit('add-todo', this.enteredTask)
    }
  }
}
</script>
