<template>
    <section>
        <h3 class="item__heading">{{ changeObj.title }}</h3>
        <ul class="item__list"
          :key="editKey"
        >
            <edit-todo
                @task-change="changeTask"
                @del-todo="todoDel"
                v-for="(todo, index) in changeObj.todoArr"
                @change-checked="checkedChenge(index)"
                :key="todo.name"
                :id="index"
                :todo="todo.name"
                :active="todo.active"
                :todoArr="changeObj.todoArr"
            >
            </edit-todo>
            <button class="item__btn item__btn-save"
            @click="addTaskEdit">
              + add todo
            </button>
            <edit-add
            ref="editAddRef"
            v-show="isActive"
            @add-todo="todoAdd">
            </edit-add>
        </ul>
        <div>
          <h3>Actions with a note</h3>
          <div>
            <button class="item__btn item__btn-save"
              @click="$emit('save-obj')"
            >
              save changes
            </button>
            <button class="item__btn item__btn-edit"
            @click="confirmCencel">
              cancel editing
            </button>
          </div>
          <div>
            <button class="item__btn item__btn-delete"
            @click="confirmDelete">
              delete
            </button>
            <button class="item__btn item__btn-change"
              @click="undoChange"
            >
              undo the change made
            </button>
          </div>
          <button class="item__btn item__btn-change"
            @click="parseLocalSt"
          >
            redo the undone change
          </button>
        </div>
        <the-confirmation v-show="changeObj.confirm">
          <p>Are you sure you want to undo your edit?</p>
          <button @click="$emit('delete-todo-edit')">Yes</button>
          <button @click="changeObj.confirm = !changeObj.confirm">Cancel</button>
        </the-confirmation>
        <the-confirmation v-show="repeal">
          <p>Are you sure you want to undo your edit?</p>
          <button @click="$emit('cencel-edit', repeal = !repeal)">Yes</button>
          <button @click="repeal = !repeal">Cancel</button>
        </the-confirmation>
    </section>
</template>

<script>
import EditTodo from './EditTodo.vue'
import TheConfirmation from './UI/TheConfirmation.vue'
import EditAdd from './EditAdd.vue'

export default {
  data () {
    return {
      repeal: false,
      isActive: false,
      editKey: 0
    }
  },
  components: {
    EditTodo,
    TheConfirmation,
    EditAdd
  },
  props: ['changeObj'],
  inject: ['localStSave'],
  emits: [
    'cheked-chenge',
    'save-obj',
    'cencel-edit',
    'delete-todo-edit',
    'undo-change',
    'save-local-st',
    'parse-local-st'
  ],
  methods: {
    checkedChenge (idx) {
      this.$emit('cheked-chenge', idx)
    },
    confirmCencel () {
      this.repeal = !this.repeal
    },
    confirmDelete () {
      this.changeObj.confirm = !this.changeObj.confirm
    },
    todoAdd (el) {
      this.changeObj.todoArr.push(el)
      this.saveLocalSt()
      this.isActive = !this.isActive
    },
    addTaskEdit () {
      this.isActive = !this.isActive
      this.$refs.editAddRef.enteredTask = {
        name: '',
        active: false
      }
    },
    todoDel (id) {
      this.changeObj.todoArr.splice(id, 1)
      this.saveLocalSt()
    },
    changeTask (id, el) {
      console.log(el)
      this.changeObj.todoArr.splice(id, 1, el)
      this.saveLocalSt()
    },
    undoChange () {
      this.$emit('undo-change', this.changeObj)
      this.forceRerenderEdit()
    },
    saveLocalSt () {
      this.$emit('save-local-st')
    },
    parseLocalSt () {
      this.$emit('parse-local-st')
      this.forceRerenderEdit()
    },
    forceRerenderEdit () {
      this.editKey += 1
    }
  }
}
</script>

<style lang="scss">
.item__btn {
  &-save {
    background: green;
  }
  &-change {
    background: black;
  }
}
</style>
