<template>
    <form @submit.prevent="submitChange">
        <input type="text"
            :for="id"
            placeholder="change tasks"
            v-model="name">
        <button>Save</button>
    </form>
</template>

<script>
export default {
  data () {
    return {
      name: ''
    }
  },
  props: ['id', 'todo'],
  emits: ['change-task'],
  methods: {
    submitChange () {
      this.$emit(
        'change-task',
        this.id,
        this.name
      )
    }
  }
}
</script>
