<template>
    <div>
        <ul class="header-tabs">
            <li class="header-tabs__item"
            v-for="(tab, idx) in tabs"
            :key="tab.title"
            @click="selectTab(idx)">
                {{ tab.title }}
            </li>
        </ul>
        <slot></slot>
    </div>
</template>

<script>
export default {
  data () {
    return {
      selectedIdx: 0,
      tabs: []
    }
  },
  props: ['selected'],
  created () {
    this.tabs = this.$children
  },
  mounted () {
    this.selectTab(0)
  },
  methods: {
    selectTab (i) {
      this.selectedIdx = i
      this.tabs.forEach((tab, idx) => {
        tab.isActive = (i === idx)
      })
    }
  }
}
</script>

<style lang="scss">
.header-tabs {
    list-style-type: none;
    width: 70vw;
    display: flex;
    justify-content: space-around;

    &__item {
        text-transform: capitalize;
        cursor: pointer;
    }
}
</style>
