<template>
  <div id="app">
    <main>
      <tabs
      ref="tabsId"
      :selected="selected">
        <tab title="home">
          <the-home
          ref="home"
          @task-edit="editTask"
          >
          </the-home>
        </tab>
        <tab title="edit">
          <the-edit
            :changeObj="changeObj"
            @cheked-chenge="chengeActiveChecked"
            @save-obj="saveChangeObj"
            @cencel-edit="editCencel"
            @delete-todo-edit="todoDeleteEdit"
            @undo-change="changeUndo"
            @save-local-st="localStSave"
            @parse-local-st="localStParse"
          >
          </the-edit>
        </tab>
      </tabs>
    </main>
  </div>
</template>

<script>
import Tabs from './components/UI/Tabs.vue'
import Tab from './components/UI/Tab.vue'
import TheHome from './components/TheHome.vue'
import TheEdit from './components/TheEdit.vue'

export default {
  name: 'App',
  components: {
    Tabs,
    Tab,
    TheHome,
    TheEdit
  },
  data () {
    return {
      selected: '',
      changeObj: {}
    }
  },
  provide () {
    return {
      localStSave: this.localStSave
    }
  },
  methods: {
    editTask (id) {
      console.log(this.changeObj)
      this.$refs.tabsId.selectTab(1)
      this.changeObj = this.$refs.home.mainArr.find((item) => item.id === id)
    },
    chengeActiveChecked (idx) {
      this.changeObj.todoArr[idx].active = !this.changeObj.todoArr[idx].active
      this.localStSave()
      console.log(this.changeObj.todoArr[idx].active)
    },
    saveObj () {
      console.log(this.changeObj)
      const idx = this.$refs.home.mainArr.findIndex((item) => item.id === this.changeObj.id)
      this.$refs.home.mainArr.splice(idx, 1, this.changeObj)
      this.$refs.home.saveTask()
    },
    saveChangeObj () {
      this.saveObj()
      this.editCencel()
    },
    editCencel () {
      this.$refs.tabsId.selectTab(0)
    },
    todoDeleteEdit () {
      this.changeObj.confirm = !this.changeObj.confirm
      const idx = this.$refs.home.mainArr.findIndex((item) => item.id === this.changeObj.id)
      this.$refs.home.mainArr.splice(idx, 1)
      this.$refs.home.saveTask()
      this.editCencel()
    },
    changeUndo (obj) {
      console.log(obj)
      const arr = JSON.parse(localStorage.getItem('mainArr'))
      this.changeObj = arr.find((item) => item.id === obj.id)
      console.log(this.changeObj)
    },
    localStSave () {
      const parsed = JSON.stringify(this.changeObj)
      localStorage.setItem('changeObj', parsed)
    },
    localStParse () {
      if (localStorage.getItem('changeObj')) {
        try {
          this.changeObj = JSON.parse(localStorage.getItem('changeObj'))
        } catch (e) {
          localStorage.removeItem('changeObj')
        }
      }
    }
  }
}
</script>

