<template>
    <li>
      <input type="checkbox"
        :name="todo"
        :value="id"
        v-model="check"
      >
      <label :for="id">{{ todo }}</label>
      <button class="item__btn item__btn-delete"
        @click="$emit('del-todo', id)"
      >
        -
      </button>
      <button class="item__btn item__btn-edit"
        @click="editAdd"
      >
        edit
      </button>
      <edit-task
        v-show="isActive"
        @change-task="taskChange"
        :id="id"
        :todo="todo">
      </edit-task>
    </li>
</template>

<script>
import EditTask from './EditTask.vue'

export default {
  components: {
    EditTask
  },
  data () {
    return {
      check: this.active,
      enteredTask: {
        name: '',
        active: false
      },
      isActive: false
    }
  },
  props: ['todo', 'todoArr', 'id', 'active'],
  emits: [
    'change-checked',
    'del-todo',
    'task-change'
  ],
  watch: {
    check () {
      this.$emit('change-checked', this.id)
    }
  },
  methods: {
    taskChange (id, name) {
      this.enteredTask.name = name
      this.$emit('task-change', id, this.enteredTask)
    },
    editAdd () {
      this.isActive = !this.isActive
    }
  }
}
</script>
