<template>
    <section class="home">
        <h1 class="home-heading">My assignments</h1>
        <button
          class="home-button"
          @click="chengeAddForm"
        >add note</button>
        <todo-data
          ref="todoDataRef"
          v-show="isActive"
          @add-todo="addTodo"
        >
        </todo-data>
        <ul class="home-list"
        :key="componentKey">
          <home-item
            @edit-task="editTask"
            @confirm-delete="confirmDelete"
            @delete-task="deleteTask"
            v-for="item in mainArr"
            :key="item.id"
            :id="item.id"
            :title="item.title"
            :todoArr="item.todoArr"
            :mainArr="mainArr"
            :confirm="item.confirm"
          >
          </home-item>
        </ul>
    </section>
</template>

<script>
import HomeItem from './HomeItem.vue'
import TodoData from './TodoData.vue'

export default {
  components: {
    HomeItem,
    TodoData
  },
  data () {
    return {
      componentKey: 0,
      isActive: false,
      mainArr: [
        {
          id: 'shop',
          title: 'shop',
          todoArr: [
            {
              name: 'milk',
              active: false
            },
            {
              name: 'eggs',
              active: false
            },
            {
              name: 'pepsi',
              active: false
            },
            {
              name: 'apple',
              active: false
            }
          ],
          confirm: false
        },
        {
          id: 'to do list',
          title: 'to do list',
          todoArr: [
            {
              name: 'breakfast',
              active: false
            },
            {
              name: 'wash the car',
              active: false
            },
            {
              name: 'go to work',
              active: false
            },
            {
              name: 'buy product',
              active: false
            }
          ],
          confirm: false
        }
      ],
      obj: {}
    }
  },
  mounted () {
    this.saveTask()
    if (localStorage.getItem('mainArr')) {
      try {
        this.mainArr = JSON.parse(localStorage.getItem('mainArr'))
      } catch (e) {
        localStorage.removeItem('mainArr')
      }
    }
  },
  methods: {
    chengeAddForm () {
      this.isActive = !this.isActive
      this.$refs.todoDataRef.tasksArr.splice(0, this.$refs.todoDataRef.tasksArr.length)
      this.$refs.todoDataRef.enteredTitle = ''
    },
    addTodo (title, todoArr) {
      const newActiveTask = {
        id: new Date().toISOString(),
        title: title,
        todoArr: todoArr,
        confirm: false
      }
      this.mainArr.push(newActiveTask)
      this.isActive = !this.isActive
      this.saveTask()
    },
    saveTask () {
      const parsed = JSON.stringify(this.mainArr)
      localStorage.setItem('mainArr', parsed)
      this.forceRerender()
    },
    deleteTask (taskId) {
      this.mainArr = this.mainArr.filter((task) => task.id !== taskId)
      this.saveTask()
    },
    confirmDelete (id) {
      this.obj = this.mainArr.find((item) => item.id === id)
      this.obj.confirm = !this.obj.confirm
    },
    editTask (id) {
      this.$emit('task-edit', id)
    },
    forceRerender () {
      this.componentKey += 1
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  padding-top: 3rem;

  &-button {
    text-transform: uppercase;
    padding: 0.5rem;
    margin-top: 1rem;
    border-radius: 15px;
  }
}
</style>
