<template>
    <form @submit.prevent="submitData">
        <div>
            <input type="text"
            placeholder="enter a title for the note"
            v-model="enteredTitle">
        </div>
        <form @submit.prevent="addTasks">
            <div>
                <input type="text"
                placeholder="enter tasks"
                v-model="enteredTask.name">
                <button>Add task</button>
            </div>
        </form>
        <div>
            <button>Save</button>
        </div>
    </form>
</template>

<script>

export default {
  emits: ['add-todo'],
  data () {
    return {
      enteredTitle: '',
      enteredTask: {
        name: '',
        active: false
      },
      tasksArr: []
    }
  },
  provide () {
    return {
      addTasks: this.addTasks
    }
  },
  methods: {
    submitData () {
      this.$emit(
        'add-todo',
        this.enteredTitle,
        this.tasksArr
      )
    },
    addTasks () {
      this.tasksArr.push(this.enteredTask)
      this.enteredTask = {
        name: '',
        active: false
      }
    }
  }
}
</script>
